<template>
    <div>
        <div>
            <b-modal id="modal-sm" size="sm" no-close-on-backdrop title="Confirm">
                <p class="my-2">
                    Overall price might differ depending on the stamp price on the day of approving the order. Agree and continue to place order?
                </p>
                <template v-slot:modal-footer="{ cancel }">
                    <b-button size="sm" class="float-right mb-3 p-2 pl-5 pr-5" variant="outline-primary" @click="cancel()">
                        Cancel
                    </b-button>
                    <b-button size="sm" class="float-right mb-3 p-2 pl-5 pr-5" variant="primary" @click="placeOrder">
                        Confirm
                    </b-button>
                </template>
            </b-modal>
        </div>
        <div class="d-block header-section">
            <h1>
                <b>Place Order</b>
            </h1>
        </div>
        <div class="container-fluid productManagement">
            <!-- START: Card Data-->
            <div class="card" style="background: transparent;margin-bottom: 20px">
                <table id="tab"
                       class="table manufactrer orderListing"
                       style="background: transparent;margin-bottom:5px">
                    <thead style="background: #FFF">
                        <tr>
                            <th>Product Type</th>
                            <th v-if="!isTpsaf">Product Name</th>
                            <th v-if="!isTpsaf">SKU</th>
                            <th v-if="!isTpsaf && !supplierNA">Supplier Name</th>
                            <th>Reel Size</th>
                            <th>No.of Reels</th>
                            <th class="text-center">Total Stamps</th>
                            <th class="text-center">Stamp Price (in GHC)</th>
                            <th class="text-center">Total (GHC)</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr class="manufacturer_add">
                            <td>
                                <select @change="getDetails" class="form-control" v-model="selectedProductType" name="productType">
                                    <option disabled selected value="0">-select-</option>
                                    <option v-for="item in productTypeList" :value="{ id: item.productId, text: item.productName }" :key="item.productId">{{ item.productName }} </option>
                                </select>
                            </td>
                            <td v-if="!isTpsaf">
                                <select class="form-control" :disabled="!selectedProductType" v-model="selectedProductName" name="productName">
                                    <option disabled selected value="0">-select-</option>
                                    <option v-for="item in productNameList" :value="{ id: item.productId, text: item.productName }" :key="item.productId">{{ item.productName }}</option>
                                </select>
                            </td>
                            <td v-if="!isTpsaf">
                                <select class="form-control" :disabled="!selectedProductType" v-model="selectedskuAlcololList" name="sku">
                                    <option disabled selected value="0">-select-</option>
                                    <option v-for="item in skuAlcoholList" :value="{ id: item.productId, text: item.productName }" :key="item.productId">{{ item.productName }}</option>
                                </select>
                            </td>
                            <td v-if="!isTpsaf && !supplierNA">
                                <select class="form-control" :disabled="!selectedProductType" v-model="selectedsupplierList" name="supplier">
                                    <option disabled selected value="0">-select-</option>
                                    <option v-for="item in supplierList" :value="{ id: item.productId, text: item.productName }" :key="item.productId">{{ item.productName }}</option>
                                </select>
                            </td>
                            <td>
                                <select @change="getTotalStamp"
                                        class="form-control" v-model="selectedReelSize" :disabled="!selectedProductType" name="coilSize">
                                    <option disabled selected value="0">-select-</option>
                                    <option v-for="item in reelSizeList" :value="{ id: item.productId, text: item.productName }" :key="item.productId">{{ item.productName }}</option>
                                </select>
                            </td>
                            <td>
                                <input @change="getTotalStamp" class="form-control" type="number" v-model="noOfReels" :disabled="!selectedProductType" min="1" />
                            </td>
                            <td class="text-center">
                                {{ totalStamp }}
                            </td>
                            <td  class="text-center">
                                {{ pricePerStamp }}
                            </td>
                            <td class="text-center">
                                {{ totalStampPrice }}
                            </td>
                            <td style="width:110px">
                                <button type="button" @click="resetRow" class="btn btn-link"><img src="@/assets/images/icons/clear1.png" class="mr-1" /> </button>
                                <button v-show="!editEnabled" type="button" @click="insertToTable" class="btn btn-link" :disabled="!enalbeInsertOrderBtn()"> <img src="@/assets/images/icons/new-add.png" class="mr-1" /> </button>
                                <button v-show="editEnabled" type="button" @click="updateRecord" class="btn btn-link" :disabled="!selectedProductType"> <!-- <img src="@/assets/images/icons/new-add.png" class="mr-1" /> --> <span>Update</span> </button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div class="card" v-show="orderListing.length > 0">
                <table id="tab" class="table orderListing">
                    <tbody>
                        <tr v-for="(item, index) in orderListing" :key="index">
                            <!-- <td>
                                 <img :src="item.imagePath" class="mr2" />{{ item.productTypeID }}
                            </td> -->
                            <td v-if="!isTpsaf">{{ item.productTypeText }}</td>
                            <td v-if="!isTpsaf">{{ item.productNameText }}</td>
                            <td v-if="!isTpsaf">{{ item.productSKUText }}</td>
                            <td v-if="!isTpsaf && !supplierNA">{{ item.productSupplierText }}</td>
                            <td>{{ item.productCoilSizeText }}</td>
                            <td>{{ item.coilCount }}</td>
                            <td>{{ item.totalStamp }}</td>
                            <td>{{ item.pricePerStamp }}</td>
                            <td>{{ item.totalStampPrice }}</td>
                            <td style="width:190px">
                                <button type="button" @click="editRow(index)" class="btn btn-link"><img src="@/assets/images/icons/new-edit.png" class="mr-1" /></button>
                                <button type="button" @click="deleteRow(index)" class="btn btn-link"> <img src="@/assets/images/icons/new-delete.png" class="mr-1" /> </button>
                            </td>
                        </tr>
                        <tr>
                            <td></td>
                            <td></td>
                            <td v-if="!isTpsaf"></td>
                            <td v-if="!isTpsaf && !supplierNA"></td>
                            <td><b>Total</b></td>
                            <td><b>{{this.totalCoils}}</b></td>
                            <td><b>{{this.totalStamps}}</b></td>
                            <td></td>
                            <td><b>{{this.totalPrice}}</b></td>
                            <td></td>
                        </tr>
                        <tr>
                            <td></td>
                            <td></td>
                            <td v-if="!isTpsaf"></td>
                            <td v-if="!isTpsaf && !supplierNA"></td>
                            <td><b>Tax</b></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td class="text-left text-primary"><b>{{this.taxAmount}}</b></td>
                            <td></td>
                        </tr>
                        <tr>
                            <td></td>
                            <td></td>
                            <td v-if="!isTpsaf"></td>
                            <td v-if="!isTpsaf && !supplierNA"></td>
                            <td><b>Shipping Charges</b></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td class="text-left text-primary"><b>{{this.shippingCharges}}</b></td>
                            <td></td>
                        </tr>
                        <tr>
                            <td></td>
                            <td></td>
                            <td v-if="!isTpsaf"></td>
                            <td v-if="!isTpsaf && !supplierNA"></td>
                            <td><b>Total</b></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td class="text-left text-primary"><b>{{this.finalAmount}}</b></td>
                            <td></td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <!-- START: Card Data-->
            <div>
                <p class="mt-4">Select Warehouse</p>
                <div class="row">
                    <div class="col-md-3" :class="{ 'form-group--error': $v.selectedWarehouse.$error }">
                        <select class="form-control" v-model="selectedWarehouse">
                            <option disabled selected value="0">-select-</option>
                            <option v-for="item in warehouseList" :value="item.productId" :key="item.productId">{{ item.productName }}</option>
                        </select>
                        <p v-if="chooseWarehouseError"> <span class="text-danger" v-if="!$v.selectedWarehouse.required">Warehouse is required</span></p>
                    </div>
                </div>
            </div>
            <div>
                <p class="mt-4">Notes</p>
                <div class="row">
                    <div class="col-md-12">
                        <textarea class="form-control" rows="5" v-model="notes"></textarea>
                        <b-button v-on:click="placingOrder()" class="float-right mt-3 mb-3 p-2 pl-5 pr-5" variant="primary" :disabled="orderListing.length == 0"> {{ this.editFlow ? "UPDATE ORDER" : "PLACE ORDER"}} </b-button>
                        <b-button v-b-modal. class="float-right mt-3 mb-3 mr-2 p-2 pl-5 pr-5" variant="outline-primary" @click="redirectToHome">CANCEL</b-button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import { required } from "vuelidate/lib/validators";
    import roles from "@/router/roles";
    export default {
        data: () => ({
            editEnabled: false,
            selectedProductType: null,
            selectedProductName: null,
            selectedskuAlcololList: null,
            selectedsupplierList: null,
            selectedWarehouse: "",
            selectedReelSize: null,
            chooseWarehouseError: false,
            noOfReels: 0,
            pricePerStamp: 0.05,
            totalStamp: 0,
            totalStampPrice: 0,
            totalCoils: 0,
            totalPrice: 0,
            totalStamps: 0,
            totalStampsPrice: 0,
            taxPercentage: 0,
            taxAmount: 0,
            shippingCharges: 0,
            finalAmount: 0,
            productTypeList: [],
            productNameList: [],
            skuAlcoholList: [],
            skuTobaccoList: [],
            supplierList: [],
            warehouseList: [],
            reelSizeList: [],
            orderListing: [],
            productOrigin: [],
            notes: "",
            editFlow: false,
            supplierNA: false,
        }),
        props: ['orderId'],
        mounted() {
            if (this.orderId) {
                this.editFlow = true;
                axios.get("/Orders", {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                    params: { orderId: this.orderId }
                }).then((response) => {
                    response.data.orderItems.map((item) => {
                        var newElem = {
                            productTypeID: item.productId,
                            productTypeText: item.productName,
                            productNameID: item.brandProductId,
                            skuID: item.stockKeepingUnitId,
                            supplierID: item.supplierId,
                            coilSizeID: item.reelSizeId,
                            coilCount: item.noOfCoils,
                            totalStamp: item.noOfStamps,
                            pricePerStamp: item.stampPrice,
                            totalStampPrice: item.totalPrice,
                            productNameText: item.brandProductName,
                            productSKUText: item.stockKeepingUnitName,
                            productSupplierText: item.supplierName,
                            productCoilSizeText: item.reelSize,
                            id: item.id
                        };
                        this.orderListing.push(newElem);
                    });
                    this.selectedWarehouse = response.data.warehouseId;
                    this.totalCoils = response.data.totalCoils;
                    this.totalPrice = response.data.totalPrice;
                    this.totalStamps = response.data.totalStamps;
                    this.totalStampsPrice = response.data.totalStampsPrice;
                    this.notes = response.data.notes;
                });
            } else {
                this.editFlow = false;
            }
        },
        validations: {
            selectedWarehouse: { required }
        },
        created() {
            this.getProducts();
            this.getProductOrigin();
            this.getStampPrice();
            this.getWarehouses();
        },
        computed: {
            isTpsaf: function () {
                return this.$store.state.clientAppDecodedToken.Role == roles.TpsafAdmin || this.$store.state.clientAppDecodedToken.Role == roles.TpsafFacilityAdmin || this.$store.state.clientAppDecodedToken.Role == roles.TpsafFacilityIncharge ;
            }
        },
        methods: {
            redirectToHome() {
                this.$router.push({ name: 'Orders' });
            },
            enalbeInsertOrderBtn() {
                if (this.isTpsaf) {
                    if (this.selectedProductType && this.selectedReelSize)
                        return true;
                }
                else if (this.supplierNA) {
                    if (this.selectedProductType && this.selectedProductName && this.selectedskuAlcololList && this.selectedReelSize)
                        return true;
                }
                else if (this.selectedProductType && this.selectedProductName && this.selectedskuAlcololList
                    && this.selectedsupplierList && this.selectedReelSize)
                    return true;
                return false;
            },
            placingOrder() {
                this.$v.$touch();
                this.chooseWarehouseError = this.$v.selectedWarehouse.$anyError;
                if (!this.chooseWarehouseError) {
                    this.$root.$emit('bv::show::modal', 'modal-sm');
                }
            },
            placeOrder() {
                var ordersListingReq = [];
                if (this.editFlow) {
                    this.orderListing.map((item) => {
                        var order = {
                            "id": item.id,
                            "productId": parseInt(item.productTypeID),
                            "brandProductId": parseInt(item.productNameID),
                            "stockKeepingUnitId": parseInt(item.skuID),
                            "reelSizeId": parseInt(item.coilSizeID),
                            "supplierId": parseInt(item.supplierID),
                            "noOfCoils": parseInt(item.coilCount)
                        }
                        ordersListingReq.push(order);
                    });
                }
                else {
                    this.orderListing.map((item) => {
                        var order = {
                            "productId": parseInt(item.productTypeID),
                            "brandProductId": parseInt(item.productNameID),
                            "stockKeepingUnitId": parseInt(item.skuID),
                            "reelSizeId": parseInt(item.coilSizeID),
                            "supplierId": parseInt(item.supplierID),
                            "noOfCoils": parseInt(item.coilCount)
                        }
                        ordersListingReq.push(order);
                    });
                }
                var reqBody = {};
                if (this.editFlow) {
                    reqBody = reqBody = {
                        "id": this.orderId,
                        "notes": this.notes,
                        "orderItems": ordersListingReq,
                        "warehouseId": parseInt(this.selectedWarehouse)
                    };
                } else {
                    reqBody = reqBody = {
                        "notes": this.notes,
                        "orderItems": ordersListingReq,
                        "warehouseId": parseInt(this.selectedWarehouse)
                    };
                }
                axios.post("/Orders",
                    reqBody,
                    {
                        baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
                    }
                ).then(() => {
                    this.$router.push({ name: 'Orders' });
                });
            },
            getProducts() {
                axios.get("/Meta/GetProducts", {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
                }).then((response) => {
                    var tempProductType = [];
                    for (let key in response.data) {
                        tempProductType.push({ 'productId': key, 'productName': response.data[key] });
                    }
                    this.productTypeList = tempProductType;
                });
            },
            getProductOrigin() {
                axios.get("/Meta/GetProductOrigns", {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
                }).then((response) => {
                    // var tempProductType = [];
                    // for (let key in response.data) {
                    //     tempProductType.push({'productId': key, 'productName': response.data[key]});
                    // }
                    this.productOrigin = response.data;
                });
            },
            getBrandProducts(selectedProductId) {
                axios.get("/Meta/GetBrandProducts", {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                    params: { productId: selectedProductId }
                }).then((response) => {
                    var tempProductName = [];
                    for (let key in response.data) {
                        tempProductName.push({ 'productId': key, 'productName': response.data[key] });
                    }
                    this.productNameList = tempProductName;
                });
            },
            getSkus(selectedProductId) {
                axios.get("/Meta/GetSkus", {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                    params: { productId: selectedProductId }
                }).then((response) => {
                    var tempSKU = [];
                    for (let key in response.data) {
                        tempSKU.push({ 'productId': key, 'productName': response.data[key] });
                    }
                    this.skuAlcoholList = tempSKU;
                });
            },
            getReelSizes(selectedProductId) {
                axios.get("/Meta/GetReelSizes", {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                    params: { productId: selectedProductId }
                }).then((response) => {
                    var tempReelSize = [];
                    for (let key in response.data) {
                        tempReelSize.push({ 'productId': key, 'productName': response.data[key] });
                    }
                    this.reelSizeList = tempReelSize;
                });
            },
            getWarehouses() {
                axios.get("/Meta/GetWarehouses", {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
                }).then((response) => {
                    var tempWarehouses = [];
                    for (let key in response.data) {
                        tempWarehouses.push({ 'productId': key, 'productName': response.data[key] });
                    }
                    this.warehouseList = tempWarehouses;
                });
            },
            getSuppliers() {
                axios.get("/Meta/GetSuppliers", {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
                }).then((response) => {
                    var tempSupplier = [];
                    for (let key in response.data) {
                        tempSupplier.push({ 'productId': key, 'productName': response.data[key] });
                    }
                    this.supplierList = tempSupplier;
                });
            },
            getStampPrice(selectedProductId) {
                axios.get("/Meta/GetStampPrice", {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                    params: { productId: selectedProductId },
                }).then((response) => {
                    this.pricePerStamp = response.data;
                });
            },
            getShippingCharges() {
                axios.get("/Meta/GetShippingCharges", {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
                }).then((response) => {
                    this.shippingCharges = parseInt(response.data);
                });
            },
            getTax() {
                axios.get("/Meta/GetTax", {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
                }).then((response) => {
                    this.taxPercentage = parseInt(response.data);
                });
            },
            insertToTable() {
                var productImage = this.selectedProductType.text
                    .toLowerCase()
                    .includes("imported")
                    ? "@/assets/images/icons/" +
                    this.selectedProductType.text.toLowerCase().split(" ")[0] +
                    "-imported.png"
                    : "@/assets/images/icons/" +
                    this.selectedProductType.text.toLowerCase().split(" ")[0] +
                    "-domestic.png";
                var newElem = {
                    imagePath: productImage,
                    productTypeID: this.selectedProductType.id,
                    productTypeText: this.selectedProductType.text,
                    productNameID: this.selectedProductName.id,
                    skuID: this.selectedskuAlcololList.id,
                    supplierID: this.selectedsupplierList.id,
                    coilSizeID: this.selectedReelSize.id,
                    coilCount: this.noOfReels,
                    totalStamp: this.totalStamp,
                    pricePerStamp: this.pricePerStamp,
                    totalStampPrice: this.totalStampPrice,
                    productNameText: this.selectedProductName.text,
                    productSKUText: this.selectedskuAlcololList.text,
                    productSupplierText: this.selectedsupplierList.text,
                    productCoilSizeText: parseInt(this.selectedReelSize.text)
                };
                this.orderListing.push(newElem);
                this.resetRow();
                this.calculateTotal();
            },
            updateRecord() {
                this.orderListing[this.editIndex].productTypeID = this.selectedProductType.id;
                this.orderListing[this.editIndex].productTypeText = this.selectedProductType.text;
                this.orderListing[this.editIndex].productNameID = this.selectedProductName.id;
                this.orderListing[this.editIndex].productNameText = this.selectedProductName.text;
                this.orderListing[this.editIndex].skuID = this.selectedskuAlcololList.id;
                this.orderListing[this.editIndex].productSKUText = this.selectedskuAlcololList.text;
                this.orderListing[this.editIndex].supplierID = this.selectedsupplierList.id;
                this.orderListing[this.editIndex].productSupplierText = this.selectedsupplierList.text;
                this.orderListing[this.editIndex].coilSizeID = this.selectedReelSize.id;
                this.orderListing[this.editIndex].productCoilSizeText = this.selectedReelSize.text;
                this.orderListing[this.editIndex].coilCount = this.noOfReels;
                this.orderListing[this.editIndex].totalStamp = this.totalStamp;
                this.orderListing[this.editIndex].pricePerStamp = this.pricePerStamp;
                this.orderListing[this.editIndex].totalStampPrice = this.totalStampPrice;
                this.editEnabled = false;
                this.editIndex = "";
                this.resetRow();
                this.calculateTotal();
            },
            resetRow() {
                this.editEnabled = false;
                this.editIndex = "";
                this.selectedProductType = "";
                this.selectedProductName = "";
                this.selectedskuAlcololList = "";
                this.selectedsupplierList = "";
                this.selectedReelSize = "";
                this.noOfReels = 0;
                this.totalStamp = 0;
                this.pricePerStamp = 0;
                this.totalStampPrice = 0;
            },
            deleteRow(index) {
                this.orderListing.splice(index, 1);
            },
            editRow(index) {
                this.editEnabled = true;
                this.editIndex = index;
                this.selectedProductType = { id: this.orderListing[index].productTypeID, text: this.orderListing[index].productTypeText };
                if (!this.isTpsaf) {
                    this.getBrandProducts(this.selectedProductType.id);
                    this.getSkus(this.selectedProductType.id);
                    this.getSuppliers();
                }
                this.getReelSizes(this.selectedProductType.id);
                this.getShippingCharges();
                this.getTax();
                this.selectedProductName = { id: this.orderListing[index].productNameID, text: this.orderListing[index].productNameText };
                this.selectedskuAlcololList = { id: this.orderListing[index].skuID, text: this.orderListing[index].productSKUText };
                this.selectedsupplierList = { id: this.orderListing[index].supplierID, text: this.orderListing[index].productSupplierText };
                this.selectedReelSize = { id: this.orderListing[index].coilSizeID, text: this.orderListing[index].productCoilSizeText };
                this.noOfReels = this.orderListing[index].coilCount;
                this.totalStamp = this.orderListing[index].totalStamp;
                this.pricePerStamp = this.orderListing[index].pricePerStamp;
                this.totalStampPrice = this.orderListing[index].totalStampPrice;
            },
            getDetails() {
                this.selectedProductName = "";
                this.selectedskuAlcololList = "";
                this.selectedsupplierList = "";
                this.selectedReelSize = "";
                this.noOfReels = 0;
                this.totalStamp = 0;
                this.pricePerStamp = 0;
                this.totalStampPrice = 0;
                if (this.productOrigin && this.productOrigin[this.selectedProductType.id] !== undefined && this.productOrigin[this.selectedProductType.id] == "Domestic")
                    this.supplierNA = true;
                else
                    this.supplierNA = false;
                if (!this.isTpsaf) {
                    this.getBrandProducts(this.selectedProductType.id);
                    this.getSkus(this.selectedProductType.id);
                    this.getSuppliers();
                }
                this.getStampPrice(this.selectedProductType.id);
                this.getReelSizes(this.selectedProductType.id);
                this.getShippingCharges();
                this.getTax();
            },
            getTotalStamp() {
                this.totalStamp = this.noOfReels * parseInt(this.selectedReelSize.text);
                this.totalStampPrice = (this.pricePerStamp * this.totalStamp);
            },
            calculateTotal() {
                this.totalCoils = this.orderListing.reduce(function (total, count) {
                    return total + parseInt(count.coilCount); // returns object with property x
                }, 0);
                this.totalStamps = this.orderListing.reduce(function (total, count) {
                    return total + parseInt(count.totalStamp); // returns object with property x
                }, 0);
                this.totalPrice = this.orderListing.reduce(function (total, count) {
                    return total + parseInt(count.totalStampPrice); // returns object with property x
                }, 0);
                this.taxAmount = (this.taxPercentage * this.totalPrice) / 100;
                this.finalAmount = this.totalPrice + this.taxAmount + this.shippingCharges;
            }
        },
    };
</script>